.Myprof {
  border: 2px solid #854ce6;
  width: 150px;
  height: 150px;
  border-radius: 100px;
  cursor: pointer;
  transition: width 1.62s ease-out, height 1.62s ease-out;
  animation: anim 8s ease-out infinite;
  
}

@keyframes anim {
  0% {
       }
 
  10% {
    border-radius: 100%;
    }
  40% {
    border-radius: 50% 50% 53% 47% / 72% 69% 31% 28%;
  }
  70% {
    border-radius: 50% 50% 53% 47% / 26% 22% 78% 74%;
   }
  80% {
    border-radius: 50% 50% 53% 47% / 26% 69% 31% 74%;
  }
  
  100% {
    }
}
.profile {
    display: flex;
    justify-content: center;
 
}
.after.enprof{
    background-color: #854ce6;
    box-shadow: #854ce6;
}

.Myprof.enlarged {
  width: 370px;
  height: 370px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  z-index: 100;
  position: fixed;
  top: 34%;
  left: 50%;
  transform: translate(-50%, -30%);
  transition: width 0.2s ease-out; 
  animation: none;
}
