body {
  background-color: #191924;
  background: #191924;
  border: 1px solid gold;
  margin: .5% 1%;
  border-radius: 10px;
}
 

.mainClassNarrow {
  margin-top: 10%;
}

.NameContainer {
  transition: all 0.5s;
  animation: fadeIn 1s forwards;
  display: flex;
  font-size: 1%;
  justify-content: center;

}

.NameContainer h1 {
  /* font-weight: bold; */
  font-family: cursive;
  color: #854ce6;
  border-right: 2px solid lightcoral;
  animation: blink 0.7s step-end infinite;
  font-size: 1.5rem;
}

@keyframes blink {

  from,
  to {
    border-color: transparent;
  }

  50% {
    border-color: #854ce6;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.gridCont {
  display: grid;
  
  gap: 10px;
  text-align: center;
 
  margin: 0 10%;
  grid-template-columns: 1fr;
  position: relative;
  color: var(--glow-color);
  cursor: pointer;
  padding: 0.35em 1em;
  border: 0.15em solid var(--glow-color);
  border-radius: 0.45em;
  background: none;
  perspective: 2em;
  font-family: "Raleway", sans-serif;
  font-size: 2em;
  font-weight: 900;
 
}


.gridCont a {
  text-decoration: none;
  color: white;
    margin-right: -0.8em;
  
  }
.page {
  filter: blur(7px);
  pointer-events: none;
  cursor: not-allowed;
}

.TwoHalves {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
}

.NavBar {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 8px;
  padding: 10px;
  position: relative; 
  width: 100%;
  transition: top 0.3s;
}
 
.NavBar ul {
  display: flex;
  font: bold 11px "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-size: small;
  padding: 0;
  margin: 0;
  list-style: none;
}

.NavBar ul li {
  flex-shrink: 0;
}

.NavBar ul li a {
  background: inherit;
  border: 1px solid #854ce6;
  text-align: center;
  color: #854ce6;
  padding: 12px 20px;
  text-decoration: none;
  white-space: nowrap;
}



.NavBar li:first-child a {
  border-radius: 10px 0 0 10px;
}

.NavBar li:last-child a {
  border-radius: 0px 10px 10px 0px;
}

.NavBar ul li a:hover {

  background-color: #29223f;
}

@media (max-width: 1200px) {
  .NavBar ul li a {
    font-size: 1.2vw;
    padding: 10px 18px;
  }
}

@media (max-width: 800px) {
  .NavBar ul li a {
    font-size: 1.5vw;
    padding: 8px 16px;
  }
}

@media (max-width: 500px) {
  .NavBar ul li a {
    font-size: 2vw;
    padding: 6px 14px;
  }
}

 
.ComponentArena {
  margin: 10px;
  width: 100%;
  padding: 10px;
  font-size: small;
  position: relative;
  transition: all 8s ease-in;
}

.ComponentArena-enter {
  transform: translateY(100%);
}

.ComponentArena-enter-active {
  transform: translateY(0);
  transition: all 0.8s ease;
}

.ComponentArena-exit {
  transform: translateY(0);
}

.ComponentArena-exit-active {
  transform: translateY(100%);
  transition: all 0.8s ease;
}

.ComponentArena p {
  padding: 15px 0;
  color: #2e64d0;
}
.btn {
  display: inline-block;
  font-style: bold;
  font-weight: 400;
  color: #854cc7;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color 0.9s ease, background-color 0.5s ease, border-color 0.5s ease;
}

.btn:hover {
  color: white;
  background-color: #854cc7;
  border-color: #854cc7;
}

.btn:focus,
.btn.focus {
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(62, 24, 115, 0.5);
}

.btn-outline-secondary {
  color: #854cc7;
  border-color: #854cc7;
}

.btn-outline-secondary:hover {
  color: white;
  background-color: #854cc7;
  border-color: #854cc7;
}

.btn-outline-secondary:focus,
.btn-outline-secondary.focus {
  box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.5);
}
.NavBar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1000; /* Adjust as needed to layer correctly with other elements */
  backdrop-filter: blur(9px);
 
}

.NavBar ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
 }

.NavBar li {
  margin: 0 .5px;
}
